import React, {useState} from 'react'
import { PrinterOutlined, CloseOutlined, CheckOutlined, CloseCircleOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { Card, Table, Button, message, } from 'antd';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import Flex from 'components/shared-components/Flex'
import { DataService } from "services/taxes.service";

const { Column } = Table; 
const PayRoll = (props) => { 	
	let history = useHistory();	
	const RedirectBack = () => {
		history.push(`/app/apps/employees/list`)
	}	
	
	const { employeesLoading, employees, allEmployees } = props;

	let totalPaye = employees.docs.reduce((accumulator, currentValue) => accumulator + allEmployees[currentValue.data()._id]?.paye, 0);
	let totalPension = employees.docs.reduce((accumulator, currentValue) => accumulator + allEmployees[currentValue.data()._id]?.pension, 0);
	let totalMaternity = employees.docs.reduce((accumulator, currentValue) => accumulator + allEmployees[currentValue.data()._id]?.maternity, 0);
	let totalCbhi = employees.docs.reduce((accumulator, currentValue) => accumulator + allEmployees[currentValue.data()._id]?.cbhi, 0);

	const handlePaid = () =>{
		let month = parseInt(new Date().getMonth()+1) < 10 ? '0'+parseInt(new Date().getMonth()+1) : parseInt(new Date().getMonth()+1);
		let date = month+'-'+new Date().getFullYear();
		let payeId = new Date().setDate(1);
		payeId = new Date(payeId).setHours(0,0,0,0);
		let payeData = { name:'PAYE',amount:totalPaye,date }
      	DataService.create(payeData,payeId)
		message.success(`Employee PAYE taxes added to paid taxes`);

		let pensionId = new Date().setDate(2);
		pensionId = new Date(pensionId).setHours(0,0,0,0);
		let pensionData = { name:'PENSION',amount:totalPension,date }
      	DataService.create(pensionData,pensionId)
		message.success(`RSSB PENSION taxes added to paid taxes`);

		let maternityId = new Date().setDate(3);
		maternityId = new Date(maternityId).setHours(0,0,0,0);
		let maternityData = { name:'MATERNITY',amount:totalMaternity,date }
      	DataService.create(maternityData,maternityId)
		message.success(`RSSB MATERNITY taxes added to paid taxes`);

		let cbhiId = new Date().setDate(4);
		cbhiId = new Date(cbhiId).setHours(0,0,0,0);
		let cbhiData = { name:'CBHI',amount:totalCbhi,date }
      	DataService.create(cbhiData,cbhiId)
		message.success(`RSSB MATERNITY taxes added to paid taxes`);

	}
	return (
		<div className="container">
			<Card>
				<div className="d-md-flex justify-content-md-between">
					<div>
						<img src="/img/logo.png" alt=""/>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">Great lakes</span><br />
								<span>Month: 2024-04</span><br />
							</p>
						</address>
					</div>
					<div className="mt-3 text-right">
						<h2 className="mb-1 font-weight-semibold">Employee Payroll</h2>
					</div>
				</div>
				

				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Employee Taxes</h2>
					<Table dataSource={employeesLoading ? [] : employees.docs } pagination={false} className="mb-5">
						
						<Column title="Name" 
							render={(_,record) => record.data().name} 
							key="name"
						/>

						<Column title="ID Number" 
							render={(_,record) => record.data().idNumber} 
							key="idnumber"
						/>

						<Column title="Type" 
							render={(_,record) => record.data().type} 
							key="type"
						/>
						
						
						<Column title="Gross Salary" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.data().salary} 
									thousandSeparator={true}
								/>
							)} 
							key="salary"
						/>

						<Column title="Net Salary" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.data().netsalary} 
									thousandSeparator={true}
								/>
							)} 
							key="salary"
						/>

						<Column title="PAYE" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={allEmployees[record.data()._id]?.paye} 
									thousandSeparator={true}
								/>
							)} 
							key="paye"
						/>

						<Column title="Pension" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={allEmployees[record.data()._id]?.pension} 
									thousandSeparator={true}
								/>
							)} 
							key="pension"
						/>
						<Column title="Maternity" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={allEmployees[record.data()._id]?.maternity} 
									thousandSeparator={true}
								/>
							)} 
							key="maternity"
						/>
						<Column title="CBHI" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={allEmployees[record.data()._id]?.cbhi} 
									thousandSeparator={true}
								/>
							)} 
							key="cbhi"
						/>
						{/* <Column 
							title="Unit Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().price} 
									suffix={allCurrencyLoading ? '' : allCurrency[garage.data().currency]?.name}
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						<Column 
							title="Total Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().total} 
									suffix={allCurrencyLoading ? '' : allCurrency[garage.data().currency]?.name}
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						 */}
					</Table>
				</div>
				
				<div className="mt-3">
					
					<div className="d-flex justify-content-end mt-4">
						<div className="text-right ">
							
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">PAYE: </span>
								<NumberFormat 
									displayType={'text'} 
									value={totalPaye} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
                            <h2 className="font-weight-semibold mt-3">
								<span className="mr-1">PENSION: </span>
								<NumberFormat 
									displayType={'text'} 
									value={totalPension} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
                            <h2 className="font-weight-semibold mt-3">
								<span className="mr-1">MATERNITY: </span>
								<NumberFormat 
									displayType={'text'} 
									value={totalMaternity} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
                            <h2 className="font-weight-semibold mt-3">
								<span className="mr-1">CBHI: </span>
								<NumberFormat 
									displayType={'text'} 
									value={totalCbhi} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
						</div>
					</div>
				</div>

				<hr className="d-print-none"/>
				<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<ArrowLeftOutlined /> Back
					</Button>
					<Button type="primary" className="mr-2" onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
					<Button type="primary" className="mr-2" onClick={handlePaid}>
						<CheckOutlined />
						<span className="ml-1">Add to taxes paid</span>
					</Button>
				</div>

			</Card>
		</div>
	);
}

export default PayRoll
