import React, {useState} from 'react'
import { Card, Table, Button, Tag, message, Menu } from 'antd';
import UserView from './UserView';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined } from '@ant-design/icons';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import Flex from 'components/shared-components/Flex'
import NumberFormat from 'react-number-format';
import PayRoll from './PayRoll';
import { useHistory } from "react-router-dom";
import { DataService } from "services/employees.service";
import { Typography } from "antd";

const { Title } = Typography;

const ProductList = ({systemData}) => {
	let history = useHistory();
	const { employees, employeesLoading, allEmployees, allEmployeesLoading } = systemData;
	const [ showPayroll, setShowPayroll ] = useState(false);
	const [totalPaye, setTotalPaye] = useState(0);
	const [totalPension, setTotalPension] = useState(0);
	const [totalMaternity, setTotalMaternity] = useState(0);
	const [totalCbhi, setTotalCbhi] = useState(0);
	const [selectedMonth, setMonth] = useState(0);

	const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => deleteRow(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);
	
	const addProduct = () => {
		history.push(`/app/apps/employees/add`)
	}

	const viewPayroll = () => {
		setShowPayroll(true);
	}
	
	const viewDetails = row => {
		history.push(`/app/apps/employees/edit/${row._id}`)
	}
	
	const deleteRow = row => {
		DataService.remove(row._id)
		message.success({ content: `Employee ${row.name} deleted `, duration: 3 });
	}

	const tableColumns = [
		{
			title: 'Employee',
			dataIndex: 'name',
			render: (_, record) => (
				<>{record.data().name}</>
			),
		},
		{
			title: 'Phone',
			dataIndex: 'phoneNumber',
			render: (_, record) => (
				<div>
					<NumberFormat
						displayType={'text'} 
						value={record.data().phoneNumber} 
						prefix={'0'} 
						thousandSeparator={false} 
					/>
				</div>
			),
		},
		
		{
			title: 'Type',
			dataIndex: 'type',
			render: (_, record) => (
				<>{record.data().type}</>
			),
		},
		{
			title: 'ID Number',
			dataIndex: 'idNumber',
			render: (_, record) => (
				<>{record.data().idNumber}</>
			),
		},
		{
			title: 'Gross Salary',
			dataIndex: 'salary',
			render: (_, record) => {
			  return (
				<NumberFormat
				  displayType={'text'} 
				  value={record.data().salary} 
				  thousandSeparator={true} 
				  suffix=' Rwf'
				/>
			  )
			}
		},
		{
			title: 'Net Salary',
			dataIndex: 'netsalary',
			render: (_, record) => {
			  return (
				<NumberFormat
				  displayType={'text'} 
				  value={record.data().netsalary} 
				  thousandSeparator={true} 
				  suffix=' Rwf'
				/>
			  )
			}
		},
		{
			title: 'Document',
			dataIndex: 'document',
			render: (_, record) => (
				<>{record.data().document}</>
			),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
	];

	return (
		<>
		{!showPayroll ? <Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Employees</Title>
					</div>
				</Flex>
				<div>
					<Button onClick={viewPayroll} size="small" className='mx-2'>View Payroll</Button>
					<Button onClick={addProduct} size="small" type="primary" icon={<PlusCircleOutlined />}>New Employee</Button>
				</div>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!employeesLoading ? employees.docs : []} 
					loading={employeesLoading}
					rowKey='_id' 
				/>
			</div>
		</Card>
		:
		<PayRoll employees={employees} employeesLoading={employeesLoading} allEmployees={allEmployees}/>
		}
		</>
	)
}

export default ProductList
