import React, { useState, useEffect } from 'react'
import PageHeaderAlt from 'components/layout-components/PageHeaderAlt'
import { Tabs, Form, Button, message } from 'antd';
import Flex from 'components/shared-components/Flex'
import GeneralField from './GeneralField'
import { useHistory } from "react-router-dom";
import { useCollection } from "react-firebase-hooks/firestore";
import { DataService } from "services/employees.service";
import LocationData from "assets/data/district.json"

const { TabPane } = Tabs;

const getBase64 = (img, callback) => {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

const ADD = 'ADD'
const EDIT = 'EDIT'

const EmployeeForm = props => {

	const { mode = ADD, param } = props

	const [form] = Form.useForm();
	const [uploadedImg, setImage] = useState('')
	const [provinceData] = useState(LocationData);
	const [uploadLoading, setUploadLoading] = useState(false)
	const [submitLoading, setSubmitLoading] = useState(false)
	const [employees, loading] = useCollection(DataService.getAll());
	const [currentId, setCurrentId] = useState('');
	const [currentPhone, setCurrentPhone] = useState('');
	const [document, setDocument] = useState('');
	const [imageState, setImageState] = useState({
		image: null,
		progress:0,
		downloadURL: ''
	});
	
	let history = useHistory();

	useEffect(() => {
    	if(mode === EDIT) {
			const { id } = param
			DataService.getRow(id).then(data =>{
				let user = data.data()
				form.setFieldsValue({
					name: user.name,
					idNumber: user.idNumber,
					phoneNumber: user.phoneNumber,
					email: user.email,
					salary: user.salary,
					netsalary: user.netsalary,
					type: user.type,
				});
				setCurrentId(user.idNumber);
				setCurrentPhone(user.phoneNumber);
				setImage(user.file)
				
				setImageState({
					image: user.file,
					progress:100,
					downloadURL: user.file
				})
			});
		}
  	}, [form, mode, param, props, provinceData]);

	const handleUploadChange = info => {
		if (info.file.status === 'uploading') {
			setImage('')
			setUploadLoading(true)
			let file = info.file.originFileObj;
			
			getBase64(file, imageUrl =>{
				setImageState({
					...imageState,
					downloadURL: imageUrl
				})
				setImage(imageUrl)
				setUploadLoading(false)  
			});
			return;
		}
	};

	const onDiscard = () => {
		history.push(`/app/apps/employees`)
	}

	const onFinish = () => {
		let idNumber = form.getFieldValue('idNumber');
		let phoneNumber = form.getFieldValue('phoneNumber');
		const verifyId = loading ? [] : employees.docs.filter( employee => employee.data().idNumber === idNumber);
		const verifyPhone = loading ? [] : employees.docs.filter( employee => employee.data().phoneNumber === phoneNumber);
		idNumber += '';
		phoneNumber += '';
		let phoneSliced = phoneNumber.slice(0,2);
		if(phoneNumber.length !== 9){
			message.error('Phone number should be 10 numbers');
			return;
		}
		if(phoneSliced !== '72' && phoneSliced !== '73' && phoneSliced !== '78' && phoneSliced !== '79'){
			message.error('Invalid phone number');
			return;
		}
		
		// if(imageState.downloadURL === ""){
		// 	message.error('Upload employee document');
		// 	return;
		// }
		if(verifyId.length > 0 && idNumber !== ''+currentId){
			message.error('Id Number is already registered');
			return;
		}
		if(verifyPhone.length > 0 && phoneNumber !== ''+currentPhone){
			message.error('Phone Number is already registered');
			return;
		}
		
		setSubmitLoading(true)
		form.validateFields().then(values => {
			
			setTimeout(() => {
				let userData = {
					name : values.name,
					phoneNumber: values.phoneNumber,
					idNumber: values.idNumber,
					email: values.email ? values.email : '' ,
					type: values.type,
					salary: values.salary,
					netsalary: values.netsalary,
					file: imageState.downloadURL,
				}
				if(mode === ADD) {
					userData.status = true
					userData.truck = false
					DataService.create(userData)
					setSubmitLoading(false)
					message.success(`${values.name} registered`);
					onDiscard();
				}
				if(mode === EDIT) {
					let { id } = param
					DataService.update(id,userData)
					setSubmitLoading(false)
					message.success(`${values.name} updated`);
					onDiscard();
				}
			}, 1500);
		}).catch(info => {
			setSubmitLoading(false)
			message.error('Please enter all required field ');
		});
	};

	return (
		<>
			<Form
				layout="vertical"
				form={form}
				name="advanced_search"
				className="ant-advanced-search-form"
				initialValues={{
					heightUnit: 'cm',
					widthUnit: 'cm',
					weightUnit: 'kg'
				}}
			>
				<PageHeaderAlt className="border-bottom" overlap>
					<div className="container">
						<Flex className="py-2" mobileFlex={false} justifyContent="between" alignItems="center">
							<h2 className="mb-2">{mode === 'ADD'? 'Add Employee' : `Edit Employee`} </h2>
							<div className="mb-3">
								<Button className="mr-2" onClick={() => onDiscard()}>Cancel</Button>
								<Button type="primary" onClick={() => onFinish()} htmlType="submit" loading={submitLoading} >
									{mode === 'ADD'? 'Add' : `Save`}
								</Button>
							</div>
						</Flex>
					</div>
				</PageHeaderAlt>
				<div className="container">
					<Tabs defaultActiveKey="1" style={{marginTop: 30}}>
						<TabPane tab="Employee Form" key="1">
							<GeneralField 
								uploadedImg={uploadedImg} 
								uploadLoading={uploadLoading} 
								handleUploadChange={handleUploadChange}
								initialState={document}
								handleStates={setDocument}
							/>
						</TabPane>
					</Tabs>
				</div>
			</Form>
		</>
	)
}

export default EmployeeForm
