import React, {useState} from 'react'
import { Input, Row, Col, Card, Form, Upload, InputNumber, message, Select } from 'antd';
import { FileAddOutlined, LoadingOutlined } from '@ant-design/icons';

const { Dragger } = Upload;
const { Option } = Select;

const rules = {
	name: [
		{
			required: true,
			message: 'Please enter employee name',
		}
	],
	idNumber: [
		{
			required: true,
			message: 'Please enter id number'
		}
	],
	phoneNumber: [
		{
			required: true,
			message: 'Please enter phone number',
		}
	],
	emailAddress: [		
		{
			required: false,
			message: 'Please enter a valid email',
		}
	],
	type: [
		{
			required: true,
			message: 'Please select employee type',
		}
	],
	salary: [
		{
			required: true,
			message: 'Please enter employee gross salary',
		}
	],
	netsalary: [
		{
			required: true,
			message: 'Please enter employee net salary',
		}
	],
}

const beforeUpload = file => {
  const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  if (!isJpgOrPng) {
    message.error('You can only upload JPG/PNG file!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('Image must smaller than 2MB!');
  }
  return isJpgOrPng && isLt2M;
}

const GeneralField = props => {
	const userRoles = ["Driver", "Staff"];
	const imageUploadProps = {
		name: 'file',
		multiple: false,
		listType: "picture-card",
		showUploadList: false,
	  }

	const handleRoleChange = value => {
		props.handleStates(value == 'Driver' ? 'driving license' : 'diploma')
	};

	return (
		
	<Row gutter={16}>
		
		<Col xs={24} sm={24} md={16}>
			<Card>
			<Row gutter={16}>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="name" label="Employee name" rules={rules.name}>
							<Input placeholder="Employee Name" />
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="idNumber" label="Id / Passport / License" rules={rules.idNumber}>
							<Input
								className="w-100"
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="phoneNumber" label="Phone Number" rules={rules.phoneNumber}>
							<InputNumber
								className="w-100"
								formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
							/>
						</Form.Item>
					</Col>
					
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="type" label="Employee Type" rules={rules.roles}>
							<Select className="w-100" placeholder="-- Select --" onChange={handleRoleChange}>
								{
									userRoles.map(elm => (
										<Option key={elm} value={elm}>{elm}</Option>
										))
								}
							</Select>							
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="salary" label="Gross Salary" rules={rules.salary}>
							<InputNumber
								className="w-100"
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="netsalary" label="Net Salary" rules={rules.netsalary}>
							<InputNumber
								className="w-100"
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
				</Row>
				
			</Card>
		</Col>
		<Col xs={24} sm={24} md={7}>
			<Card title={props.initialState ? 'Upload employee '+props.initialState : 'Upload document'}>
				<Dragger {...imageUploadProps} beforeUpload={beforeUpload} onChange={e=> props.handleUploadChange(e)}>
					{
						props.uploadedImg ? 
						<img src={props.uploadedImg} alt="avatar" className="img-fluid" /> 
						: 
						<div>
							{
								props.uploadLoading ? 
								<div>
									<LoadingOutlined className="font-size-xxl text-primary"/>
									<div className="mt-3">Uploading</div>
								</div> 
								: 
								<div>
									<FileAddOutlined className="display-3"/>
									
									<p>Click or drag image to upload</p>
								</div>
							}
						</div>
					}
				</Dragger>
			</Card>
		</Col>
	</Row>
)}

export default GeneralField
